@import "../../../Variables.scss";

.HeaderContainer {
  background-color: $TopBackground;
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  a {
    text-decoration: none;
  }
}

.HeaderLogoContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 10px;
  cursor: pointer;
}

.HeaderNavigationList {
  list-style: none;
  display: flex;
  margin: 0 10px;
  height: 100%;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  padding-left: 0;
}

.HeaderTitle {
  font-size: x-large;
  margin: 10px;
  color: white;
}

@media screen and (max-width: 750px) {
  .HeaderContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
    cursor: pointer;
  }

  .HeaderLogoContainer {
    padding-top: 0;
  }

  .HeaderNavigationList li {
    padding-bottom: 5px;
  }
}
